/**
 ****** mainFun() ******
 @ jquery
 @ 
 @ ex:goToUp()
 @ author: 277040350@qq.com
 @ 2016-5-10
 */
var $ = require('jquery');
var mainFun = function(){
	'use strict';  //ECMAScript 5
	this.versions = "1.1.0";  //
	this.loading = $('<div class="loading-gif"></div>');  //loading
	this.loading_cell = $('<div class="loading-type swiper-slide" id="loading-type"><i class="loading-small"></i></div>');
	/**
	 @ 
	 @ 2<div class="close" data-dome="dome1" data-black="dome2"></div>
	 @ param: data-dome -- classid
	 @ param: data-black -- 
	 @ return: null
	 */
	$("body .close,body #close").on('click', function(event) {
		'use strict';  //ECMAScript 5
		var dome = $(this).data('dome'),
			black = $(this).data('black');
		black && $('.'+black).fadeOut();
	 		$('.'+dome) && $('.'+dome).remove();
	 		$('#'+dome) && $('#'+dome).remove();
	}); //
};

mainFun.prototype = {
	/**
	 @ getUrlParam:url
	 @ param: null
	 @ return: obj(array) -- url 
	 */
	getUrlParam : function(){
		'use strict';  //ECMAScript 5
		var src = location.href,
	        data = src.substr(src.indexOf("?")+1,src.length-1),
	        obj = {};
	    $(data.split("&")).each(function(index, el) {
	        var a = el.split("=");
	        obj[a[0]] = a[1];
	    });
	    return obj;
	},
	ajax : function(param){   //ajax
		'use strict';  //ECMAScript 5
		$.ajax({
			url: param.url,
			type: param.type,
			dataType: 'json',
			data: param.data,
			success : param.success,
			error : param.error
		});
	},
	getJSON : function(param){   //
		'use strict';  //ECMAScript 5
		$.getJSON(param.url,
            	  param.data,
                  param.success
        );
	},
	loadingStart : function(){
		$("body").append(this.loading);
	},
	loadingEnd : function(){
		$("body").remove(this.loading);
	},
	/**
	 @ timeFormat:yyyy-mm-dd hh:ss
	 @ param: time(number) -- 
	 @ return: time(string) -- 
	 */
	timeFormat : function(time){   //yy-mm-dd hh:ss
		'use strict';  //ECMAScript 5
		var formattedDate = new Date(parseInt(time)),
            y = formattedDate.getFullYear(),
            m =  formattedDate.getMonth() + 1,
            d = formattedDate.getDate(),
            h = formattedDate.getHours(),
            s = formattedDate.getMinutes();
        m = m < 10 ? "0"+m : m;
        d = d < 10 ? "0"+d : d;
        s = s < 10 ? "0"+s : s;
      	var time = y+"-"+m+"-"+d +" "+h+":"+s;
        return time;
	},
	getTranslateY : function(node){   //translateYint
		'use strict';  //ECMAScript 5
        var regRule = /translate(Y|\dd)?\(\s*(\w+\s*,)?\s*([^,]+)(\s*,[^)]+)?\s*\)/;
        var regRule2 = /matrix\(.*,\s*(\w+)\s*\)/;
        var transform = node.style.transform;
        var reg;
        if(!transform){
            return null;
        }
        reg = regRule.exec(transform);
        if(null === reg){
            reg = regRule2.exec(transform);
            return reg ? reg[1] : null;
        }
        return reg[3].replace("px","")*1;
    },
    /**
	 @ formValidation:
	 @ param: dome(string) -- (id/class)
	 @ return: booleans
	 */
    formValidation : function(dome){
    	'use strict';  //ECMAScript 5
	    var $this = this;
	    var dome = dome || this.msg("dom"),
	        action = true;
	    $(dome+" input").each(function(index, el) {
	        var req = $(this).attr("data-required"),
	            text_req = $(this).attr("text-required"),
	            reg = RegExp($(this).attr("data-pattern")),
	            text_reg = $(this).attr("text-pattern"),
	            val = $(this).val();
	            if (action && req && text_req && (val == null || val == '')){
	                $this.msg(text_req);
	                action = false;
	                return false;
	            }else if(action && reg && text_reg && (val != null || val != '') && !reg.test(val)){ 
	                $this.msg(text_reg);
	                action = false;
	                return false;
	            }
	    });
	    return action;
	},
	/**
	 @ timeDown:(object)
	 @ objectParam: dome(string) -- (id/class)
	 @ objectParam: time(number) -- 
	 @ objectParam: callback(function) -- 
	 @ return: null
	 */
	timeDown : function(setting){
		'use strict';  //ECMAScript 5
	    setting = $.extend(true,{
	        time  : 60,
	        callback : function(){
	            this.msg("");
	        }
	    },setting);
	    var dome = $(setting.dome);
	    dome.html(setting.time);
	    var vali = setInterval(function(){
	        var s = dome.text() * 1;
	        s--;
	        if(s == 0){
	            clearInterval(vali);
	            setting.callback && setting.callback();
	        }
	        dome.html(s < 10 ? "0"+s : s);
	    }, 1000);
	},
	/**
	 @ msg:
	 @ param: text(string) -- 
	 @ param: callback(function) -- 
	 @ return: null
	 */
	msg : function(text,callback){   //msg
		'use strict';  //ECMAScript 5
		if(text && "string" == typeof text){
			var box = $("<div class='popup-msg'>"+text+"</div>"),
				z = new Date().getTime();
			$("body").append(box);
			box.attr("style","z-index:"+z).animate({
				"opacity" : 1
			},2500).animate({
				"opacity" : 0
			},500,function(){
				$(this).remove();
				setTimeout(function(){
					callback && "function" == typeof callback && callback();
				}, 600);
			});
		}
	},
	/**
	 @ iosGetJs:IOSjs
	 @ param: callback -- IOS
	 @ return: null
	 */
	iosGetJs : function(callback) {
        if (window.WebViewJavascriptBridge) {
            return callback(WebViewJavascriptBridge);
        }
        if (window.WVJBCallbacks) {
            return window.WVJBCallbacks.push(callback);
        }
        window.WVJBCallbacks = [callback];
        var WVJBIframe = document.createElement('iframe');
        WVJBIframe.style.display = 'none';
        WVJBIframe.src = 'wvjbscheme://__BRIDGE_LOADED__';
        document.documentElement.appendChild(WVJBIframe);
        setTimeout(function() {
            document.documentElement.removeChild(WVJBIframe)
        }, 0)
    },
    /**
	 @ isIOS:ios
	 @ param: null
	 @ return: Boolean
	 */
    isIOS : function(){
    	return (navigator.userAgent.match("GGXUECHE") && navigator.userAgent.match("iOS"));
    },
    /**
	 @ isAndroid:ios
	 @ param: null
	 @ return: Boolean
	 */
    isAndroid : function(){
    	return (navigator.userAgent.match("GGXUECHE") && navigator.userAgent.match("Android"));
    },
    appBtn : function(name){
    	try {
    		this.isIOS && this.iosGetJs(function(bridge) {
	            bridge.callHandler(name);
	        });
	        this.isAndroid && window.stub[name]();
    	} catch(e) {
    		this.msg(e);
    	}
    }
}
//
module.exports = mainFun;


