const React           = require('react'),
	  ReactDOM        = require('react-dom'),
	  HeaderCell      = require('./include/header'),   // header
	  NavCell         = require('./include/nav'),      // nav
	  MessageNavCell  = require('./include/messageNav'),  // messageNav
	  BannerCell      = require('./include/banner'),  // banner
	  MessageListCell = require('./include/messageList'),
	  DownloadApp     = require('./include/downloadApp'),  // downloadApp
	  FooterCell      = require('./include/footer'),  // footer
	  MainFun         = require('mainFun');  // 
	  require("../css/style.scss");  // 

const MessageHtml = React.createClass({
		componentDidMount: function(){  // domcomponentDidMountdom
			//var mainFun  = new MainFun();  // 
		},
		render: function(){
			return (
				<div>
					<DownloadApp href="" />
					<HeaderCell />
					<NavCell />
					<MessageNavCell />
					<BannerCell />
					<MessageListCell />
					<FooterCell />
				</div>
			);
		}
});
ReactDOM.render(
	<MessageHtml />,
	document.getElementById('content-cell')
);


